@import url('https://fonts.googleapis.com/css2?family=Lato:wght@300;400;700&display=swap');

body{
    margin:0px;
    padding: 0px;
    background-color: #000000;
    font-family: 'Lato', sans-serif;
}
*{
    box-sizing: border-box;
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
}
button{
    outline: none;
    border: none;
}
input{
    outline: none;
    border: none;
}

.logo img{
    width: 130px;
    height: 80px;
}

.header-heading {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    color: #ffffff;
    z-index: 1;
}

.header-heading h2 {
    font-size: 2.5rem;
    margin-bottom: 0;
}

.header-heading h1 {
    font-size: 4rem;
    margin-top: 0;
}

.header-heading span {
    color: #e1a030;
}
.details {
    font-size: 1.2rem;
    color: #dcd16a;
    margin-bottom: 30px;
}
#main {
    width: 100%;
    height: 768px;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url('./images/banner.jpg');
    position: relative;
    z-index: 0;
}

#main::before {
    position: absolute;
    content: "";
    background: radial-gradient(ellipse at center, rgba(48, 47, 41, 0.38) 0%, rgba(76, 76, 70, 0.6) 100%);
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
}

nav{
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    left: 0;
    top: 0;
    width:100%;
    z-index: 1;
    background-color: transparent;
    transition: 0.6s ease-in-out;
}

nav.active{
    box-shadow: 5px 10px 30px rgba(0, 0, 0, 0.157);
    background-color: #000000;
}

nav ul{
    display: flex;
    margin-left: 550px;
}

.active{
    background-color: #c8b418;
    color:#ffffff;
    box-shadow: 5px 10px 30px rgba(209, 153, 32, 0.411);
    transition: all ease 0.2s;
}

.active:hover{
    background-color: #000000;
    color:#ffffff;
    box-shadow: 5px 10px 30px rgba(217, 154, 45, 0.411);
    transition: all ease 0.2s;
}

nav ul li a{
    font-family: 'Lato';
    height:40px;
    line-height: 43px;
    margin: 3px;
    padding: 0px 22px;
    display: flex;
    font-size: 0.9rem;
    text-transform: uppercase;
    font-weight: 500;
    color:#ffffff;
    letter-spacing: 1px;
    border-radius: 3px;
    transition: 0.2s ease-in-out;
}

nav ul li a:hover{
    background-color: #d9b945;
    color:#ffffff;
    box-shadow: 5px 10px 30px rgba(255, 204, 2, 0.336);
    transition: all ease 0.2s;
}

nav .menu-btn,
.menu-icon{
    display:none;
}

.name{
    text-align: center;
    font-family: 'Montserrat';
    width: 900px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}

.name span{
    color: #c9b208;
}

.name .details{
    font-size: 23px;
    color: #c5c5c5;
}

.name h2{
    font-family:'Lato';
    font-size: 2.7rem;
    margin:0px;
    letter-spacing: 2px;
    color:#ffffff;
}

.name h1{
    font-family:'Lato';
    font-size: 5rem;
    margin:0px;
    letter-spacing: 2px;
    color:#ffffff;
}

.header-btns{
    display: flex;
    margin-top: 40px;
    margin-left: 40%;
}
.header-btn{
	width:160px;
	height: 50px;
	display: flex;
	justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    background-color: #b78a19;
    box-shadow: 5px 10px 30px rgba(199, 159, 40, 0.336);
    border-radius: 5px;
	color:#ffffff;
}
.header-btn:hover{
	background-color: transparent;
    transition: all ease 0.5s;
    color: #ffffff;
    border: 2px solid #e1ba2e;
}
#features {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #000000;
    color: #fff;
    font-size: 1.5rem;
    padding-top: 40px;
    box-sizing: border-box;
    font-family: 'Lato';
    position: absolute;
   
}
#features h1{
    text-decoration: underline;
    text-decoration-color: #c9b208;
}
.a-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.a-box {
    background-color: #000000;
    width: 250px;
    height: 400px;
    margin: 40px;
    border-radius: 10px;
    overflow: hidden;
    transition: 0.4s ease-in-out;
    display: flex;
    flex-direction: column;
}

.a-box:hover {
    border: 1px solid #c4aa17;
    box-shadow: 2px 2px 12px rgba(209, 117, 24, 0.445);
}

.a-b-img {
    width: 100%;
    height: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.a-b-img img {
    width: 150px;
    height: auto;
}

.a-b-text {
    width: 80%;
    height: 60%;
    background-color: #000000;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    flex-direction: column;
    margin-top: -30px;
    margin-left: 10%;
}

.a-b-text h2 {
    color: #fff;
    font-size: 1.2rem;
}

.a-b-text p {
    color: #ffffffa6;
    font-size: 1rem;
    margin: 0;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    -webkit-box-orient: vertical;
    overflow: hidden;
}

.a-b-text p:hover {
    color: #ffffff;
}

  #offer {
    width: 100%;
    height: 768px;
    position: relative;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(images/offer.png);
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 600px;
}

#offer:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(circle at center, rgba(8, 0, 0, 0), rgba(0, 0, 0, 0.8));
}

.pr-heading{
    text-align: center;
	font-family:'Lato';
	width: 600px;
	position:absolute;
	top: 55%;
	transform: translate(-50%,-55%);
    margin-top: 15.5px;
    margin-left: 45%;
}
.pr-heading span{
    color: #e2b324;
}
.pr-heading .details{
    font-size: 20px;
    color: #ddb815;
    font-size: 1.2rem;
}
.pr-heading h1{
	font-family:'Lato';
	font-size: 4rem;
	margin:0px;
	letter-spacing: 2px;
	color:#ffffff;
    
}
.pr-heading p{
    width:100%;
    align-items: center;
    justify-content: center;
}
.pr-btns{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 40px;
}
.pr-btn{
	width:160px;
	height: 50px;
	display: flex;
	justify-content: center;
    align-items: center;
    font-size: 1.4rem;
    background-color: #e7c21f;
    box-shadow: 5px 10px 30px rgba(199, 164, 39, 0.336);
    border-radius: 5px;
	color:#ffffff;
}
.pr-btn:hover{
	background-color: transparent;
    transition: all ease 0.5s;
    color: #ffffff;
    border: 2px solid #d5b91b;
}

#about{
    margin-top:100px ;
	width:100%;
	height: 80vh;
	box-sizing: border-box;
	display: flex;
	align-items: center;
	justify-content: space-between;
	position: relative;
}

.about-text{
    width: 45%;
    color: #fff;
    list-style: circle;
}
.about-text h1 span{
    font-size: 5rem;
    color: #ddb815;
}
.about-text h1{
	font-size: 3rem;
	color:#cecece;
    font-weight: 500;
    margin: 0px;
	padding: 0px;
}
.about-text p{
    width: 80%;
	font-size: 1.2rem;
	color:#cecece;
	
}
.about-text button{
    margin-top: 20px;
	width: 140px;
	height: 45px;
	border-radius: 10px;
	border: none;
    outline: none;
    color: #ffffff;
	background-color:#ead624;
}
.about-text button:hover{
    box-shadow: 5px 10px 30px rgba(245, 210, 52, 0.336);
	background-color: rgb(0, 0, 0);
    transition: all ease 0.3s;
    color: #f1cd49;
}
.about-image{
    width: 50%;
    display: block;
}
.about-image img{
  width: 600px;
  position: relative;
}
.about-image::before {
    content: '';
    position: relative;
    left: 0;
    width: 100%;
    height: 100%;
    background: radial-gradient(ellipse at center, rgba(0,0,0,0) 0%, rgba(0,0,0,1) 100%);
}

#contact{
    width:100%;
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
#contact h1{
    color: #fff;
    font-size: 3rem;
    text-decoration: underline;
    text-decoration-color: #c4aa17;
}
#contact form{
    width:600px;
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-bottom: 40px;
}
#contact form input,
#contact form textarea{
    width:100%;
    height:50px;
    margin:5px 0px;
    padding: 10px;
    border: none;
    outline: none;
    background-color: #4d431c2d;
    color: #e2cb48;
    border-radius: 5px;
}
#contact form textarea{
    height: 150px;
}
#contact form input[type="submit"]{
    height: 45px;
    background: linear-gradient(90deg, #e3d31c 20%,#decd4c);
    color: #ffffff;
    text-transform: uppercase;
}

.footer {
    background-color: #333;
    color: #fff;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    height: 110px;
  }
  
  .logo {
    margin-left: 50px;
  }
  
  .logo img {
    width: 120px;
    height: 80px;
  }
  
  .footer-links {
    display: flex;
  }
  
  .footer-links ul {
    list-style: none;
    gap: 50px;
    margin: 0;
    display: flex;
    margin-right: 50px;
  }
  
  .footer-links ul li {
    margin-right: 20px;
    color: #c4aa17;
  }
  
  .copyright {
    margin-top: 10px;
    text-align: center;
    width: 100%;
    opacity: 70%;
    background-color: #000000;
  }
  
/* Responsive CSS */
@media screen and (max-width: 620px) {
    .footer {
      flex-direction: column;
      justify-content: center;
      height: auto;
      padding: 10px;
    }
    #offer {
      margin-top: 10%;
    }
    .logo {
      margin-left: 0;
      margin-bottom: 20px;
    }
    .footer-links ul {
      margin: 20px 0;
      justify-content: center;
      margin-right: 0;
    }
    .pr-heading {
      width: 300px;
      margin-left: -2%;
    }
    .pr-btns {
      margin-left: 100px;
      margin-bottom: 1000px; /* Modified */
    }
    #contact form {
      width: 90%;
    }
    .pr-heading,
    .pr-heading .details {
      margin-right: 1000px;
      margin-bottom: 1000px;
    }

  
  @media screen and (min-width: 620px) and (max-width: 1100px) {
    .footer-links ul {
      gap: 20px;
    }
  }
  
  @media screen and (min-width: 1100px) and (max-width: 1190px) {
    .footer-links ul {
      gap: 30px;
    }
    .pr-heading {
      width: 400px;
      margin-left: 35%;
    }
    .pr-btns {
      margin-left: 100px;
      margin-bottom: 1000px; /* Modified */
    }
  }
  
  @media (max-width: 1190px) {
    .pr-heading {
      width: 500px;
      margin-left: 40%;
    }
    #offer {
        margin-top: 2000px;
      }
    
  }
  
  @media (max-width: 1100px) {
    #main {
      background-size: 1150px !important;
    }

    .name,
    .pr-heading {
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      text-align: center;
    }
    .name h2,
    .pr-heading h2 {
      font-size: 1.5rem;
    }
    .name h1,
    .pr-heading h1 {
      font-size: 3rem;
    }
    .name .details,
    .pr-heading .details {
      font-size: 1rem;
    }
    .about-image img {
      height: 400px;
    }
    #features {
      height: auto;
    }
    .a-container {
      flex-wrap: wrap;
    }
    .a-box {
      flex-grow: 1;
    }
    .a-b-img img {
      object-fit: contain;
    }
  }
  
  @media (max-width: 970px) {
    #main {
      height: 650px;
    }
    .name,
    .pr-heading {
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
    }
    .about-image {
      display: none;
    }
    .header-btn,
    .pr-btn {
      margin-left: 20%;
      margin-top: 20px;
    }
    #about {
      justify-content: center;
      padding-top: 0px;
      text-align: center;
      margin-top: 0px;
    }
    .about-text {
      width: 90%;
    }
    .about-text h1 {
      font-size: 4rem;
    }
    .about-text p {
      width: 100%;
    }
    #features {
      margin-bottom: 100px;
    }
  }
  
  @media (max-width: 600px) {
    .name {
      width: 60%;
    }
    .pr-heading {
      width: 60%;
    }
  }
  
  @media (max-width: 1100px) {
    .menu-icon {
      display: block;
    }
    nav {
      justify-content: space-between;
      height: 65px;
      padding: 0px 30px;
    }
    .logo img {
      width: 120px;
    }
    .header-btns,
    .pr-btns {
      margin: 0;
    }
    .menu {
      display: none;
      position: absolute;
      top: 65px;
      left: 0px;
      background-color: #000000;
      border-bottom: 4px solid #ff1414;
      width: 100%;
      padding: 0px;
      margin: 0px;
    }
    .menu li {
      width: 100%;
    }
    nav .menu li a {
      width: 100%;
      height: 40px;
      justify-content: center;
      align-items: center;
      margin: 0px;
      padding: 25px;
      border: 1px solid rgba(38, 38, 38, 0.03);
    }
    nav .menu-icon {
      cursor: pointer;
      float: right;
      padding: 28px 20px;
      position: relative;
      user-select: none;
    }
    nav .menu-icon .nav-icon {
      background-color: #ffffff;
      display: block;
      height: 2px;
      position: relative;
      transition: background 0.2s ease-out;
      width: 24px;
    }
    nav .menu-icon .nav-icon:before,
    nav .menu-icon .nav-icon:after {
      background: rgb(255, 255, 255);
      content: '';
      display: block;
      height: 100%;
      position: absolute;
      transition: all ease-out 0.2s;
      width: 100%;
    }
    nav .menu-icon .nav-icon:before {
      top: 6px;
    }
    nav .menu-icon .nav-icon:after {
      top: -6px;
    }
    nav .menu-btn:checked ~ .menu-icon .nav-icon {
      background: transparent;
    }
    nav .menu-btn:checked ~ .menu-icon .nav-icon:before {
      transform: rotate(-45deg);
      top: 0;
    }
    nav .menu-btn:checked ~ .menu-icon .nav-icon:after {
      transform: rotate(45deg);
      top: 0;
    }
    nav .menu-btn {
      display: none;
    }
    nav .menu-btn:checked ~ .menu {
      display: block;
    }
  }
  
  @media (max-width: 400px) {
    .a-box {
      width: 100%;
      height: 400px;
    }
  }
  @media screen and (max-width: 620px) {
    .footer {
      flex-direction: column;
      justify-content: center;
      height: auto;
      padding: 10px;
    }
    
    .logo {
      margin-left: 0;
      margin-bottom: 20px;
    }
    
    .footer-links ul {
      margin: 20px 0;
      justify-content: center;
      margin-right: 0;
    }
    
    .pr-heading {
      width: 300px;
      margin-left: -2%;
    }
    
    .pr-btns {
      margin-left: 0; /* Adjusted */
      margin-bottom: 10px; /* Adjusted */
    }
    
    .pr-btn {
      margin-left: 0; /* Adjusted */
    }
    
    #contact form {
      width: 90%;
    }
    
    #offer{
        position:relative;
        margin-bottom: 50px;
    }
    .pr-heading,
    .pr-heading .details {
      margin-right: 1000px;
      margin-bottom: 10px;
    }
    .copyright{
        margin-top: 20px;
    }
    logo{
        margin-top: 10px;
    }
  }
  }